import uniPopup from '@/components/uni-popup/uni-popup.vue'
export default {
	components:{
		uniPopup
	},
	data() {
		return {
			rejectReason:'',															//拒绝理由
			isActive:0,																		//选中
			nodes:'<div style="text-align:center;"><img src="https://img-cdn-qiniu.dcloud.net.cn/uniapp/images/uni@2x.png"/></div>'
		};
	},
	methods:{
		// 拨打电话
		handlePhone(){
			window.location.href = "tel:110"
		},
		// 选中项
		selectItem(index){
			this.isActive = index
		},
		// 打开拒绝详情
		handleRejectOpen(){
			this.$refs.reject.open()
		},
		// 关闭简介
		handleRejectClose(){
			this.$refs.reject.close()
		}
	}
}
